import React, { Component } from 'react';
import { Container } from 'reactstrap';

export default class NotFound extends Component {
    render() {
        return(
            <div>
                <Container>
                    <h1 style={headerStyle}>
                        404
                    </h1>
                </Container>
            </div>
        )
    }
}

const headerStyle = {
    marginLeft: '50%',
    fontColor: '#fffff0'
}