import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
    Container,
    Collapse,
    Navbar,
    NavbarToggler,
    Nav,
    NavItem,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem } from 'reactstrap';
import '../css/main_nav.css';

export default class MainNav extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }
    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }
    render() {
        return (
            <div>
                <Container>
                    <Navbar expand="lg" className="main_header_nav_overrides">
                        <NavbarToggler onClick={this.toggle} color="white"><i className="fas fa-bars"></i></NavbarToggler>
                        <Collapse isOpen={this.state.isOpen} navbar>
                            <Nav navbar>
                                <NavItem style={{paddingRight: '10px'}}>
                                    <Link className="nav-link" style={{textDecoration: 'none'}} to="/">
                                        <i className="fas fa-home"></i><span style={{paddingLeft: '10px'}}>Home</span>
                                    </Link>
                                </NavItem>
                                <UncontrolledDropdown nav inNavbar style={{paddingRight: '15px'}}>
                                    <DropdownToggle nav caret>
                                        <i className="fas fa-university"></i><span style={{paddingLeft: '10px'}}>About Institute</span>
                                    </DropdownToggle>
                                    <DropdownMenu right>
                                        <DropdownItem>
                                            Link 1
                                        </DropdownItem>
                                        <DropdownItem>
                                            Link 2
                                        </DropdownItem>
                                        <DropdownItem>
                                            Link 3
                                        </DropdownItem>
                                    </DropdownMenu>
                                </UncontrolledDropdown>
                                <UncontrolledDropdown nav inNavbar style={{paddingRight: '15px'}}>
                                    <DropdownToggle nav caret>
                                        <i className="fas fa-building"></i><span style={{paddingLeft: '10px'}}>For Companies</span>
                                    </DropdownToggle>
                                    <DropdownMenu right>
                                        <DropdownItem>
                                            Link 1
                                        </DropdownItem>
                                        <DropdownItem>
                                            Link 2
                                        </DropdownItem>
                                        <DropdownItem>
                                            Link 3
                                        </DropdownItem>
                                    </DropdownMenu>
                                </UncontrolledDropdown>
                                <UncontrolledDropdown nav inNavbar>
                                    <DropdownToggle nav caret>
                                        <i className="fas fa-user-graduate"></i><span style={{paddingLeft: '10px'}}>For Students</span>
                                    </DropdownToggle>
                                    <DropdownMenu right>
                                        <DropdownItem>
                                            Link 1
                                        </DropdownItem>
                                        <DropdownItem>
                                            Link 2
                                        </DropdownItem>
                                        <DropdownItem>
                                            Link 3
                                        </DropdownItem>
                                    </DropdownMenu>
                                </UncontrolledDropdown>
                            </Nav>
                            <Nav navbar className="ml-auto">
                                <NavItem style={{right: '0'}}>
                                    <Link className="nav-link" style={{textDecoration: 'none'}} to="/contact">
                                        <i className="fas fa-address-book"></i><span style={{paddingLeft: '10px'}}>Contact Us</span>
                                    </Link>
                                </NavItem>
                            </Nav>
                        </Collapse>
                    </Navbar>
                </Container>
            </div>
        )
    }
}
