import React, { Component } from 'react';
import {
  Container,
  Row,
  Nav,
  NavItem,
  Col
} from 'reactstrap';
import PropTypes from 'prop-types';

import '../css/main_header.css';

class MainHeader extends Component {
render() {
    return (
      <div>
        <Container fluid style={{padding: 0}} className="top_nav_override">
          <Nav className="navbar">
            <NavItem className="nav-link ml-auto">
              <i className="fas fa-envelope"></i><span style={{padding: '10px'}} >placements@iitgoa.ac.in</span>
            </NavItem>
            <NavItem className="nav-link">
              <i className="fas fa-phone"></i><span style={{padding: '10px'}}>+91 12345-6789</span>
            </NavItem>
          </Nav>
        </Container>
        <Container className="container-margins">
          <Row>
            <Col sm="2" className="text-center mt-auto mb-auto">
                <img id="logo" src="http://iitgoa.ac.in/img/iitgoa_logo_wx.png" alt="Logo" />
            </Col>
            <Col sm="10" className="header float-left mt-auto mb-auto">
              <div className="text-left">
                <h1>Internship and Placement Cell</h1>
                <h2><small>INDIAN INSTITUTE OF TECHNOLOGY GOA</small></h2>
                <div id="mobile_only_text">
                  <h2><small><i className="fas fa-envelope"></i><span style={{padding: '10px'}}>placements@iitgoa.ac.in</span></small></h2>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

Container.propTypes = {
  fluid: PropTypes.bool
}
export default MainHeader;